html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  color: #53575a;
  line-height: 1.6; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  margin: 0; }

a {
  text-decoration: none; }

img {
  width: 100%;
  display: block;
  max-width: 100%; }

.l-container {
  max-width: 1024px;
  margin: 0 auto; }

.active {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 4px 8px;
  border-top: 1px solid #dfe0e0; }
  .active__title {
    text-align: center; }
  .active__photos {
    display: flex; }
  .active__indicator {
    position: relative; }
    .active__indicator::before {
      position: absolute;
      content: "";
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: #30c352;
      display: block;
      -webkit-transform: translate(2px);
              transform: translate(2px); }
  .active__img {
    height: 35px;
    width: 35px;
    border-radius: 50%;
    margin-right: 4px; }

.button {
  border: none;
  background-color: #ff1f5a;
  color: #ffffff;
  font-family: "Poppins", sans-serif;
  padding: 12px;
  font-size: 16px;
  cursor: pointer;
  outline: none;
  transition: background-color 0.3s cubic-bezier(0.54, 0.11, 0.24, 1.3);
  border-radius: 3px;
  text-shadow: 0 0.5px 1px #141618;
  text-transform: uppercase; }
  .button:hover {
    background-color: #eb003e; }
  .button--primary {
    background: #ff1f5a; }
    .button--primary:hover {
      background-color: #eb003e; }
  .button--secondary {
    background: #235789; }
    .button--secondary:hover {
      background-color: #193d60; }
  .button--terciary {
    background: #f9ff21; }
    .button--terciary:hover {
      background-color: #e7ed00; }
  .button--signout {
    position: fixed;
    bottom: 100px;
    right: 8px;
    border-radius: 50%; }
  .button--reset {
    position: fixed;
    bottom: 100px;
    left: 8px;
    border-radius: 50%; }

.dashboard {
  padding: 8px; }
  .dashboard__title {
    text-align: center;
    margin-bottom: 8px; }
  .dashboard__signout {
    position: fixed;
    bottom: 100px; }

.card {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #dfe0e0;
  justify-self: center;
  border-radius: 5px; }
  .card--disabled {
    background: #dfe0e0;
    -webkit-animation: disabled 1s cubic-bezier(0.65, 1.24, 0.48, 1.19) both;
            animation: disabled 1s cubic-bezier(0.65, 1.24, 0.48, 1.19) both; }

@-webkit-keyframes disabled {
  from {
    -webkit-transform: scale(1);
            transform: scale(1); }
  to {
    -webkit-transform: scale(0.95);
            transform: scale(0.95); } }

@keyframes disabled {
  from {
    -webkit-transform: scale(1);
            transform: scale(1); }
  to {
    -webkit-transform: scale(0.95);
            transform: scale(0.95); } }

.cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 8px; }

.results {
  margin-top: 8px; }
  .results__total {
    font-size: 2.5rem;
    margin-top: 8px; }
  .results__average {
    font-size: 3rem; }

.login {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  min-height: 100vh; }

.loader--full {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  min-height: 100v; }

.sk-cube-grid {
  width: 50px;
  height: 50px;
  margin: 100px auto; }

.sk-cube-grid .sk-cube {
  width: 33%;
  height: 33%;
  background-color: #ff1f5a;
  float: left;
  -webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
  animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out; }

.sk-cube-grid .sk-cube1 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

.sk-cube-grid .sk-cube2 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

.sk-cube-grid .sk-cube3 {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s; }

.sk-cube-grid .sk-cube4 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s; }

.sk-cube-grid .sk-cube5 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

.sk-cube-grid .sk-cube6 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

.sk-cube-grid .sk-cube7 {
  -webkit-animation-delay: 0s;
  animation-delay: 0s; }

.sk-cube-grid .sk-cube8 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s; }

.sk-cube-grid .sk-cube9 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

@-webkit-keyframes sk-cubeGridScaleDelay {
  0%,
  70%,
  100% {
    -webkit-transform: scale3D(1, 1, 1);
    transform: scale3D(1, 1, 1); }
  35% {
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1); } }

@keyframes sk-cubeGridScaleDelay {
  0%,
  70%,
  100% {
    -webkit-transform: scale3D(1, 1, 1);
    transform: scale3D(1, 1, 1); }
  35% {
    -webkit-transform: scale3D(0, 0, 1);
    transform: scale3D(0, 0, 1); } }

